<template>
  <div class="home">
    <div class="playlist">
      <HomeTitle title="推荐歌单" path="/playlists"></HomeTitle>
      <PlaylistsList :playlists="playlists"></PlaylistsList>
    </div>
    <div class="album">
      <HomeTitle title="最新专辑" path="/albums"></HomeTitle>
      <AlbumsList :albums="albums"></AlbumsList>
    </div>
    <div class="artist">
      <HomeTitle title="热门歌手" path="/artists"></HomeTitle>
      <ArtistsList :artists="artists"></ArtistsList>
    </div>
    <div class="toplist">
      <HomeTitle title="热门榜单" path="/toplists"></HomeTitle>
      <PlaylistsList :playlists="toplists" kind="toplist"></PlaylistsList>
    </div>
  </div>
</template>

<script>
import AlbumsList from "@/components/AlbumsList.vue";
import ArtistsList from "@/components/ArtistsList.vue";
import PlaylistsList from "@/components/PlaylistsList.vue";
import HomeTitle from "@/components/HomeTitle.vue";

export default {
  components: {
    AlbumsList,
    ArtistsList,
    PlaylistsList,
    HomeTitle,
  },
  data() {
    return {
      albums: [],
      artists: [],
      playlists: [],
      toplists: [],
    };
  },
  methods: {
    async getAlbums() {
      const { data: res } = await this.$http.get("/album/new", {
        params: { limit: 20 },
      });
      // console.log(res);
      this.albums = res.albums;
    },
    async getArtists() {
      const { data: res } = await this.$http.get("/top/artists", {
        params: { limit: 10 },
      });
      // console.log(res);
      this.artists = res.artists;
    },
    async getPlaylists() {
      const { data: res } = await this.$http.get("/personalized", {
        params: { limit: 20 },
      });
      // console.log(res);
      this.playlists = res.result;
      this.playlists.forEach((item) => {
        item.coverImgUrl = item.picUrl;
      });
    },
    async getToplists() {
      const { data: res } = await this.$http.get("/toplist/detail", {});
      // console.log(res);
      this.toplists = res.list.filter((item, index) => {
        return index < 10;
      });
    },
  },
  created() {
    this.getAlbums();
    this.getArtists();
    this.getPlaylists();
    this.getToplists();
  },
};
</script>

<style lang="less" scoped>
.home {
  padding: 0 50px;
}
</style>