import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
  songInfo: {},
  singleList: [],
  fullList: [],
  playlist: [],
}
const mutations = {
  setSongInfo(state, data) {
    state.songInfo = data
  },
  setSingleList(state, data) {
    state.songInfo = data
    const index = state.singleList.findIndex((item) => {
      return item.id === data.id;
    });
    if (index !== -1) {
      state.singleList.splice(index, 1)
    }
    state.singleList.push(data)
    state.playlist = state.singleList
  },
  setFullList(state, data) {
    state.songInfo = data[0]
    state.fullList = data
    state.playlist = state.fullList
  }
}

const store = new Vuex.Store({
  state,
  mutations
})

export default store
