<template>
  <div class="container">
    <div class="main">
      <ul>
        <li v-for="(item, index) in toplists" :key="index">
          <router-link :to="`/playlist/${item.id}`">
            <el-image :src="item.coverImgUrl" lazy></el-image>
            <div class="text">
              <p>{{ item.name }}</p>
              <div v-if="item.tracks.length !== 0">
                <span
                  class="tracks"
                  v-for="(items, index) in item.tracks"
                  :key="index"
                  >{{ index + 1 }}.{{ items.first }}-{{ items.second }}</span
                >
              </div>
              <div v-else>
                <span class="update">{{ item.updateFrequency }}</span>
              </div>
            </div>
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      toplists: [],
    };
  },
  methods: {
    async getToplists() {
      const { data: res } = await this.$http.get("/toplist/detail");
      // console.log(res);
      this.toplists = res.list;
    },
  },
  created() {
    this.getToplists();
  },
};
</script>

<style lang="less" scoped>
.container {
  padding: 0 50px;
  .main {
    margin-top: 30px;
    ul {
      li {
        display: inline-block;
        margin: 20px;
        width: 370px;
        height: 150px;
        overflow: hidden;
        &:hover {
          transition: all 0.5s;
          transform: translate(0, -15px);
        }
        a {
          display: flex;
          .el-image {
            width: 150px;
            height: 150px;
            border-radius: 8%;
          }
          .text {
            width: 220px;
            height: 150px;
            background-color: #E4E7ED;
            border-radius: 5%;
            p {
              margin-top: 15px;
              margin-left: 20px;
              font-size: 20px;
              font-weight: bold;
            }
            .tracks {
              display: block;
              margin: 10px;
              font-size: 15px;
              text-overflow: ellipsis;
              white-space: nowrap;
              overflow: hidden;
            }
            .update {
              display: block;
              margin: 20px;
            }
          }
        }
      }
    }
  }
}
</style>