<template>
  <div class="container">
    <div class="header">
      <div class="photo">
        <el-image :src="artist.picUrl" lazy></el-image>
      </div>
      <div class="text">
        <p>{{ artist.name }}</p>
        <span>{{ artist.briefDesc | ellipsis }}</span>
      </div>
    </div>
    <div class="main">
      <el-tabs value="first" stretch>
        <el-tab-pane label="歌曲" name="first">
          <div class="hot">
            <span>热门歌曲</span>
            <el-button type="primary" round @click="play">播放全部</el-button>
          </div>
          <SongsTable :songs="hotSongs"></SongsTable>
        </el-tab-pane>
        <el-tab-pane label="专辑" name="second">
          <AlbumsList :albums="hotAlbums"></AlbumsList>
        </el-tab-pane>
        <el-tab-pane label="MV" name="third">
          <div class="mv">
            <ul>
              <li v-for="(item, index) in mvs" :key="index">
                <a href="javascript:;">
                  <el-image :src="item.imgurl" lazy></el-image>
                  <span>{{ item.name }}</span>
                </a>
              </li>
            </ul>
          </div>
        </el-tab-pane>
        <el-tab-pane label="详情" name="fourth">
          <div class="desc">
            <span>{{ artist.briefDesc }}</span>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
import AlbumsList from "@/components/AlbumsList.vue";
import SongsTable from "@/components/SonsTable.vue";
export default {
  components: {
    AlbumsList,
    SongsTable,
  },
  props: ["id"],
  data() {
    return {
      artist: {},
      hotSongs: [],
      hotAlbums: [],
      mvs: [],
    };
  },
  methods: {
    async getArtist() {
      const { data: res } = await this.$http.get("/artists", {
        params: {
          id: this.id,
        },
      });
      // console.log(res);
      this.artist = res.artist;
      this.hotSongs = res.hotSongs;
    },
    async getHotAlbums() {
      const { data: res } = await this.$http.get("/artist/album", {
        params: {
          id: this.id,
        },
      });
      // console.log(res);
      this.hotAlbums = res.hotAlbums;
    },
    async getMvs() {
      const { data: res } = await this.$http.get("/artist/mv", {
        params: {
          id: this.id,
        },
      });
      // console.log(res);
      this.mvs = res.mvs;
    },
    play() {
      this.$store.commit("setFullList", this.hotSongs);
    },
  },
  created() {
    this.getArtist();
    this.getHotAlbums();
    this.getMvs();
  },
  watch: {
    id: {
      handler() {
        this.getArtist();
        this.getHotAlbums();
        this.getMvs();
      },
    },
  },
};
</script>

<style lang="less" scoped>
.container {
  padding: 0 50px;
  .header {
    margin: 30px 0;
    display: flex;
    .photo {
      width: 600px;
      height: 300px;
      overflow: hidden;
      margin-right: 50px;
      .el-image {
        width: 600px;
        transform: translate(0, -20%);
      }
    }
    .text {
      flex: 1;
      p {
        font-size: 30px;
        font-weight: bold;
        margin-bottom: 20px;
      }
      span {
        font-size: 15px;
        color: #606266;
      }
    }
  }
  .main {
    .hot {
      span {
        padding: 30px;
        font-size: 20px;
        font-weight: bold;
      }
    }
    .mv {
      ul {
        li {
          display: inline-block;
          width: 240px;
          height: 210px;
          overflow: hidden;
          margin: 10px 20px;

          a {
            .el-image {
              width: 240px;
              height: 160px;
            }
            span {
              display: block;
              width: 240px;
              height: 50px;
              font-size: 15px;
              font-weight: bold;
            }
          }
        }
      }
    }
    .desc {
      padding: 20px;
      font-size: 15px;
    }
  }
}
</style>