import Vue from 'vue'
import VueRouter from 'vue-router'
import Album from '@/views/album/Album.vue'
import Albums from '@/views/albums/Albums.vue'
import Artist from '@/views/artist/Artist.vue'
import Artists from '@/views/artists/Artists.vue'
import Home from '@/views/home/Home.vue'
import Layout from '@/views/Layout.vue'
import Playlist from '@/views/playlist/Playlist.vue'
import Playlists from '@/views/playlists/Playlists.vue'
import Search from '@/views/search/Search.vue'
import Toplists from '@/views/toplists/Toplists.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: Layout,
    children: [
      { path: '/', component: Home },
      { path: 'album/:id', component: Album, props: true },
      { path: 'albums', component: Albums },
      { path: 'artist/:id', component: Artist, props: true },
      { path: 'artists', component: Artists },
      { path: 'playlists', component: Playlists },
      { path: 'playlist/:id', component: Playlist, props: true },
      { path: 'search/:keywords', component: Search, props: true },
      { path: 'toplists', component: Toplists }
    ]
  },
]

const router = new VueRouter({
  routes
})

export default router
