<template>
  <div class="container">
    <div class="header">
      <div class="photo">
        <el-image :src="playlist.coverImgUrl" lazy></el-image>
      </div>
      <div class="text">
        <p>{{ playlist.name }}</p>
        <div class="tag">
          <span v-for="(item, index) in playlist.tags" :key="index"
            >#{{ item }}</span
          >
        </div>
        <span class="desc">{{ playlist.description | ellipsis }}</span>
        <el-button type="primary" round @click="play"
          >播放全部</el-button
        >
      </div>
    </div>
    <div class="main">
      <SongsTable :songs="songs"></SongsTable>
    </div>
  </div>
</template>

<script>
import SongsTable from "@/components/SonsTable.vue";
export default {
  components: {
    SongsTable,
  },
  props: ["id"],
  data() {
    return {
      playlist: {},
      songs: [],
    };
  },
  methods: {
    async getPlaylist() {
      const { data: res } = await this.$http.get("/playlist/detail", {
        params: {
          id: this.id,
        },
      });
      // console.log(res);
      this.playlist = res.playlist;
      this.songs = res.playlist.tracks;
    },
    play() {
      this.$store.commit("setFullList", this.songs);
    },
  },
  created() {
    this.getPlaylist();
  },
};
</script>

<style lang="less" scoped>
.container {
  padding: 0 50px;
  .header {
    margin: 30px 0;
    display: flex;
    .photo {
      width: 250px;
      height: 250px;
      margin-right: 50px;
      .el-image {
        width: 250px;
        height: 250px;
        border-radius: 8%;
      }
    }
    .text {
      height: 250px;
      flex: 1;
      p {
        font-size: 30px;
        font-weight: bold;
      }
      .tag {
        margin: 15px;
        span {
          margin-right: 10px;
          color: #303133;
        }
      }
      .desc {
        display: block;
        margin-bottom: 30px;
        font-size: 15px;
        color: #606266;
      }
    }
  }
}
</style>