<template>
  <div class="container">
    <el-tabs value="first" stretch>
      <el-tab-pane label="相关歌曲" name="first">
        <SongsTable :songs="songs"></SongsTable>
      </el-tab-pane>
      <el-tab-pane label="相关歌手" name="second">
        <ArtistsList :artists="artists"></ArtistsList>
      </el-tab-pane>
      <el-tab-pane label="相关专辑" name="third">
        <AlbumsList :albums="albums"></AlbumsList>
      </el-tab-pane>
      <el-tab-pane label="相关歌单" name="fourth">
        <PlaylistsList :playlists="playlists"></PlaylistsList>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import AlbumsList from "@/components/AlbumsList.vue";
import ArtistsList from "@/components/ArtistsList.vue";
import PlaylistsList from "@/components/PlaylistsList.vue";
import SongsTable from "@/components/SonsTable.vue";

export default {
  components: {
    AlbumsList,
    ArtistsList,
    PlaylistsList,
    SongsTable,
  },
  props: ["keywords"],
  data() {
    return {
      songs: [],
      artists: [],
      playlists: [],
      albums: [],
    };
  },
  methods: {
    async getSongs() {
      const { data: res } = await this.$http.get("/cloudsearch", {
        params: {
          keywords: this.keywords,
          type: "1",
        },
      });
      // console.log(res);
      this.songs = res.result.songs;
    },
    async getArtists() {
      const { data: res } = await this.$http.get("/cloudsearch", {
        params: {
          keywords: this.keywords,
          type: "100",
        },
      });
      // console.log(res);
      this.artists = res.result.artists;
    },
    async getAlbums() {
      const { data: res } = await this.$http.get("/cloudsearch", {
        params: {
          keywords: this.keywords,
          type: "10",
        },
      });
      // console.log(res);
      this.albums = res.result.albums;
    },
    async getPlaylists() {
      const { data: res } = await this.$http.get("/cloudsearch", {
        params: {
          keywords: this.keywords,
          type: "1000",
        },
      });
      // console.log(res);
      this.playlists = res.result.playlists;
    },
  },
  created() {
    this.getSongs();
    this.getArtists();
    this.getAlbums();
    this.getPlaylists();
  },
  watch: {
    keywords: {
      handler() {
        this.getSongs();
        this.getArtists();
        this.getAlbums();
        this.getPlaylists();
      },
    },
  },
};
</script>

<style lang="less" scoped>
.container {
  padding: 0 50px;
}
</style>

